import React from 'react';
import styled from 'styled-components';
import { ImPhone } from "react-icons/im";

const FooterBlock = styled.div`
   background:#444;
   width:100%;
   font-size:12px;
   color:#aaa;
   display:flex; align-items:center;
   .row{
       padding:25px 0px;
       width:100%;
       display:flex; justify-content:center; align-items:center;
       .footerLeft{
           width:80%; line-height:1.6;
           p:nth-child(2),p:nth-child(5){margin-bottom:10px;}
        }
       .footerRight{
           width:20%; text-align:center;
           .phone{font-size:30px; margin-bottom:3px;}
           p:nth-of-type(1){font-size:20px; margin-bottom:6px;}
           p:nth-of-type(2){font-size:14px;}
    
    }
`;

const Footer = () => {
    return (
        <FooterBlock>
            <div className="row">
                <div className="footerLeft">
                    <p>© 2021 Hotels.com은 Expedia Group 계열사입니다. All rights reserved.</p>
                    <p>Hotels.com 및 Hotels.com 로고는 미국 및/또는 다른 국가에서 Hotels.com, LP의 상표 또는 등록 상표입니다. 기타 모든 상표는 해당 소유권자의 자산입니다.</p>
                    <p>서울시 종로구 종로5길 Tower8 9층, Tel: 02-3480-0145, Mail: CS@koreasupport.hotels.com</p>
                    <p>트래블 파트너 익스체인지 코리아 주식회사 | 사업자등록번호 123-34-01025 | 대표이사: 홍길동</p>
                    <p>(주) 호텔스닷컴코리아 |사업자등록번호 : 717-81-00091ㅣ사업자정보확인ㅣ통신판매업신고번호 : 2015-서울종로-1067호</p>
                    <p>호텔스닷컴 웹사이트를 통해 구매할 수 있는 게재 상품 및 서비스에 대한 정보는 판매자에 의해 제공됩니다. 따라서 호텔스닷컴은 이와 관련된 정보의 정확성과 완전성 여부에 대해 책임을 지지 않습니다.</p>
                </div>
                <div className="footerRight">
                    <ImPhone className="phone" />
                    <p>02-3480-0145</p>
                    <p>호텔스닷컴 고객센터</p>
                </div>
            </div>
        </FooterBlock>
    );
};

export default Footer;