import React from 'react';
import styled from 'styled-components';

const ButtonBlock = styled.button`
    width:100%;
    padding:15px 10px;
    color:#fff;
    background:#333;
    border-radius:10px;
    border:none;
    margin-top: 20px;
    &:hover{background:#999;}
   
`;

const Button = (props) => {
    return (
        <ButtonBlock {...props} />
    );
};

export default Button;