import React from 'react';
import styled from 'styled-components';

const ProductItemBlock = styled.div`
   width:100%;
   border-top:74px solid #fff;
   border-bottom:1px solid #ddd;
   font-family: 'Noto Sans KR', sans-serif;
   width:100%;
   display:flex;
   padding-bottom:30px;
   margin-bottom:30px; align-items:center;
   img{width:25%; padding-left:20px;}
   div:nth-of-type(1){
       width:60%;
       padding:0 30px;
       p:nth-child(1){font-size:28px;}
       p:nth-child(2){font-size:14px; margin:3px 0 30px; color:#888;}
       p:nth-child(3){
           font-size:12px; 
           display:inline-block;
           margin:5px 0; padding:4px 6px;  
           color:#fff; background:#218241; border-radius:4px;
        }
        p:nth-child(4){font-size:14px;}
   }
   div:nth-of-type(2){
       width:40%;
       text-align:right;
       padding-right:20px;
     button{ 
         background:#da272c; color:#fff; padding: 16px 30px; font-size:18px; border-radius:50px;
        
        }
   }
`;

const ProductItem = ({name, depart, score, about, photo}) => {
    return (
        <ProductItemBlock>
            <img src={photo} alt={about} />
            <div>
                <p>{name}</p>
                <p>{depart}</p>
                <p>{score}</p>
                <p>{about}</p>
            </div>
            <div><button>지금 예약</button></div>
        </ProductItemBlock>
    );
};

export default ProductItem;