import React from 'react';
import styled from 'styled-components';

const UseBlock = styled.div`
    border-top:74px solid #fff;
    height:100vh;
    text-align:center;
    display:flex; justify-content:center; align-items:center; 
    img{
       width:500px; margin-bottom:20px;
    }
    h2{font-size:30px; font-weight:bold; color:#cd272d; margin-bottom:15px;}
    p{line-height:1.6; color:#888;}
`;
const Communitysection = () => {
    return (
        <UseBlock>
           <div>
               <img src="images/800px-Hotels.com_Logo.png" alt="" />
               <h2>페이지 서비스 준비중입니다.</h2>
               <p>보다 나은 서비스 제공을 위하여 페이지 준비중에 있습니다.<br />빠른 시일내에 준비하여 찾아뵙겠습니다.</p>
           </div>
        </UseBlock>
     );
};
export default Communitysection;