import React, { useState, useEffect } from 'react';
import {Link} from 'react-router-dom';
import styled from 'styled-components';
import cn from 'classnames';
import { GiHamburgerMenu } from "react-icons/gi";
import { FaUserCircle } from "react-icons/fa";

const HeaderBlock = styled.div`
    // background:skyblue;
    // box-shadow:0px 2px 2px #999;
    padding:15px 0;
    position:fixed; top:0; left:0; width:100%;  z-index:99;
    border-bottom:2px solid #da272c;
    .row {

        display:flex; 
        justify-content:space-between;
        align-items:center;
        .logo { width: 150px;}
        .nav {
            .depth1 { display:flex; font-weight:bold;
                // background:#cd272d; border-radius:50px;
                li { margin:0 20px;  position:relative; padding:10px 15px;
                    // .depth2 { display:none;
                    //     position:absolute; top:100%; left:50%; transform:translateX(-50%);
                    //     width:150px; background:#da272c; 
                    //     padding:20px 30px; border-radius:10px; 
                    //     // margin-top:3px;
                    //     li { color:#fff; padding:5px 0;}  
                    }
                &:hover>.depth2{display:block}
                }
            }
        }
        .mobileNav {

            position:relative;
            button { padding:8px 15px; 
                border-radius:50px; 
                font-size:20px; 
                background:#da272c;
                color:#fff;
                display:flex; align-items:center;
                .ham { margin-right:10px }
        
            }
            .mobileNavList {
                display:none; 
                position:absolute;
                top:100%; right:0;
                // border:1px solid #000;
                width:150px;
                margin-top:5px;
                padding:10px 0px;
                border-radius:10px; background:#cd272d; color:#fff;
                font-family: 'Noto Sans KR', sans-serif;
                // letter-spacing:1px;
                &.opener { display:block }
                ul {
                    li { padding:0 20px; line-height:30px;
                      &:nth-child(3) {
                          border-top:1px solid #ddd;
                          padding-top:10px;
                          margin-top:10px;
                      }
                    }
                }
            }
        }
    }
`;

const Header = ({onSelect}) => {

    const [opener, setOpener] = useState(false);
    const openClose = () => {
        setOpener(!opener);
    }

    const[scy, setScy] =  useState(0)
    const onScroll = () => {
        setScy(window.scrollY)
    }
    useEffect(()=>{ //리턴문을 수행하고 자동으로 호출되는 함수
        window.addEventListener("scroll", onScroll)
        return () => window.removeEventListener("scroll", onScroll)
    }, []) 

    return (
        <HeaderBlock style={{background:(scy) ? "#fff" : "transparent", color:(scy) ? "#da272c" : "#da272c"}}> 
        {/* 원래 로고 색 #cd272d */}
            <div className="row">
                <h1 className="logo">
                    <Link to="/">
                       <img src="images/800px-Hotels.com_Logo.png" alt="" />
                    </Link>
                </h1>
                <nav className="nav">
                    <ul className="depth1">
                        <li ><Link to="/product">특가상품</Link>
                            {/* <ul className="depth2">
                                <li><Link to="/product" onClick={()=>onSelect(0)}>메뉴1</Link></li>
                                <li><Link to="/product" onClick={()=>onSelect(1)}>메뉴2</Link></li>
                                <li><Link to="/product" onClick={()=>onSelect(2)}>메뉴3</Link></li>
                            </ul> */}
                        </li>
                        <li><Link to="/news">이용안내</Link>
                            {/* <ul className="depth2">
                                <li><Link to="/news">메뉴1</Link></li>
                                <li>메뉴2</li>
                                <li>메뉴3</li>
                            </ul> */}
                        </li>
                        <li ><Link to="/community">숙박시설 등록</Link>
                            {/* <ul className="depth2">
                                <li><Link to="/community">메뉴1</Link></li>
                                <li>메뉴2</li>
                                <li>메뉴3</li>
                            </ul> */}
                        </li>
                        <li ><Link to="/about">리워드</Link>
                            {/* <ul className="depth2">
                                <li><Link to="/about">메뉴1</Link></li>
                                <li>메뉴2</li>
                                <li>메뉴3</li>
                            </ul> */}
                        </li>
                    </ul>
                </nav>
                <div className="mobileNav">
                    <button onClick={openClose}>
                        <GiHamburgerMenu className="ham" />
                        <FaUserCircle className="person" />
                    </button>
                    <div className={cn("mobileNavList", {opener})}>
                        <ul>
                            <li><Link to="/login">로그인</Link></li>
                            <li><Link to="/join">회원가입</Link></li>
                            <li><Link to="/product">특가상품</Link></li>
                            <li><Link to="/news">이용안내</Link></li>
                            <li><Link to="/community">숙박시설 등록</Link></li>
                            <li><Link to="/about">리워드</Link></li>
                        </ul>
                    </div>
                </div>
            </div>
        </HeaderBlock>
    );
};

export default Header;