import React,{} from 'react';
import styled,{css} from 'styled-components';
import doctors from './doctors.json';
import ProductItem from './ProductItem';

const ProductBlock = styled.div`
    border-top:74px solid #fff;
    .tabs{display:flex; justify-content:center;}
    .content{display:flex; flex-wrap:wrap}
`;

const tabTit = [
    {id:0, title:"서울", part:doctors.part1},
    {id:1, title:"속초", part:doctors.part2},
    {id:2, title:"부산", part:doctors.part3}
]

const List = styled.div`
    padding:8px 20px; border-radius:10px;
    cursor:pointer; margin :50px 20px;
    ${
        props => props.active && css`background:#333; color:#fff;`
    }
`;

const Productsection = ({onSelect, activeTab}) => {
 
    return (
        <ProductBlock>
            <div className="row">
                <div className="tabs">
                    {
                        tabTit.map((data)=> <List key={data.id} active={activeTab===data.id} onClick={()=>onSelect(data.id)}>{data.title}</List>)
                    }
                </div>
                <div className="content">
                    {
                        tabTit[activeTab].part.map((data,index) => <ProductItem key={index} name={data.name} depart={data.depart} score={data.score} about={data.about} photo={data.photo} />)
                    }
                </div>
            </div>
        </ProductBlock>
    );
};

export default Productsection;