import React from 'react';
import {Link} from 'react-router-dom';
import styled from 'styled-components';
import {ImHome} from "react-icons/im";
import {GiReceiveMoney} from "react-icons/gi";
import {RiVipCrownFill} from "react-icons/ri";


const UseBlock = styled.div`
    border-top:74px solid #fff;
    height:100vh;
    display:flex; align-items:center;
    .row{
        text-align:center; 
        .rewardTitle{
            h1{font-size:30px; margin-bottom:5px;}
            p{color:#777}
        }
        .rewardExplain{
            ul{
                margin:50px 0;
                display:flex;

                li{
                    flex:1; margin: 40px 100px 40px 0; padding:0 8px;
                    h3{font-size: 18px; margin-bottom:10px;}
                    div{
                        margin:0px auto 30px;
                        display:flex; justify-content:center; align-items:center; 
                        width:200px; height:200px; 
                        font-size:90px; 
                        background:#e6ffff; border-radius:50%; color:#81b9bf;
                        
                    }
                    p{color:#777}
                    &:nth-child(3){margin-right:0;}
                }
            }
        }
        .rewardBtn{
            font-size:18px; font-weight:bold; color:#156bc1;
            button{
                font-size:18px; font-weight:bold;
                &:nth-of-type(1){
                    margin-right:30px; padding: 10px 20px; border-radius:50px;
                    background:#156bc1; color:#fff;
                    &:hover{background:#125ca5;}
                } 
            }
        }   


    }
`;

const Aboutsection = () => {
    return (
            <UseBlock>
               <div className="row">
                   <div className="rewardTitle">       
                       <h1>즉시 할인, 리워드* 숙박 등 다양한 혜택을 누리세요!</h1>
                       <p>리워드 프로그램을 최대한 활용할 수 있는 방법을 알아보세요.</p>
                    </div>
                    <div className="rewardExplain">
                        <ul>
                            <li>
                                <div><GiReceiveMoney /></div>
                                <h3>즉시 할인</h3>
                                <p>엄선된 숙박 시설을 비밀 가격으로 더 저렴하게 예약하실 수 있습니다.</p>
                            </li>
                            <li>
                                <div><ImHome /></div>
                                <h3>리워드* 1박 혜택</h3>
                                <p>10박을 숙박할 때마다 드립니다.</p>
                            </li>
                            <li>
                                <div><RiVipCrownFill /></div>
                                <h3>다양한 VIP Access 혜택</h3>
                                <p>Silver 회원이 되면 더 많은 혜택을 받으실 수 있어요.</p>
                            </li>
                        </ul>
                    </div>   
                   <div className="rewardBtn">
                       <button>지금 가입</button>
                       <Link to="/Login">로그인</Link>
                   </div>
               </div>
            </UseBlock>
    );
};

export default Aboutsection;