import React,{useState} from 'react';
import './Homesection.scss';
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import styled from 'styled-components';
import {ImLocation} from "react-icons/im";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css"; 

const Article1 = styled.div`
    position:relative;
    width:100%; height:100%;
    .reserve{ 
        box-shadow: 0px 0px 30px rgba(0,0,0,0.3);
        z-index:1;
        display:flex;
        align-items:center;
        position:absolute;
        top:50%; left:50%;
        transform:translate(-50%,-50%);
        background:#fff;
        margin:0 auto;
        width:100%; height:280px;
        border-radius:150px;
        .reserveAll{
            margin-left:80px;
            p{ 
                color: #cd272d;
                font-size: 32px;
                font-weight:bold;
                margin-bottom:30px;
            }

            .reserveBox{
                display:flex; 
                width:1000px;
                .locate{
                    display:inline-block;
                    padding-right:10px;
                    border:1px solid rgb(118,118,118);
                    border-radius:2px;
                    margin-right:5px;
                    height:50px;
                    font-size:20px;
                   
                    #locate{
                        border:none;
                        font-size:20px;
                        width:150px;
                          height:48px;
                          padding-left: 20px;
                          ImLocation{top:}
                    }
    
                } 
                #people{        
                    height:50px; padding-left: 20px; font-size:20px;
                    margin-right:5px;
                    width:150px;
                    padding-right:10px;
                 
                }
                #submit{
                    width:100px; height:50px; background:#cd272d; color:#fff; 
                    border-radius:3px; font-size:16px;
                }
                .react-datepicker-wrapper{
                    margin-right:5px;
                    input{width:100%; height:50px; font-size:20px; padding-left: 20px; color:rgb(133,133,133)}
                }

            }
            
          
        }
    }
    .slide{ position:relative;
        height:100vh !important;
        background-size:cover;
        background-repeat:no-repeat;
        background-position:center;
        filter: grayscale(0.4);

        &.slide1{background-image:url('images/slide2.jpg')};
        &.slide2{background-image:url('images/slide1.jpg')};
        &.slide3{background-image:url('images/slide4.jpg')};
        .text{
            position:absolute; top:50%; left:50%;
            transform:translate(-50%,-50%);
            color:#fff; font-size:30px;
            button{padding:10px; border:none; background:#fff; border-radius:10px;}
        }
    }
    .slick-dots { position:absolute; bottom:30px; left:50%; transform:translate(-50%);
        li{display:inline-block; padding:0 5px;
            button{width:30px; height:30px; border-radius:50%; background:#fff;
                text-indent:-99999px; overflow:hidden;
            }
            &.slick-active{ button{background:red;}}
        }
    };

    


`; 


const Homesection = () => {
    const settings = {
        dots:false,
        infinite:true,
        speed:3000,
        autoplay:true,
        autoplaySpeed:8000,
        slidesToShow:1,
        slidesToScroll:1,
        // prevArrow: false,
        // nextArrow: false
        arrows:false

    }

    const [startDate, setStartDate] = useState(new Date());
    const [endDate, setEndDate] = useState(new Date());
    return (
        <div className="homeWrap">
            <Article1 className="article1">
                <div className="reserve row">
                    <div className="reserveAll"> 
                       <p>다양한 호텔을 최적의 요금으로 예약해보세요</p>
                       <div className="reserveBox"> 
                            <div className="locate"><input type="text" id="locate" name="locate" placeholder="도시명"></input><ImLocation /></div>
                            <DatePicker class="kim" selected={startDate} onChange={date => setStartDate(date)} />
                            <DatePicker class="kim" selected={endDate} onChange={date => setEndDate(date)} />
                            <input type="number" id="people" name="people" placeholder="인원" min="0" />
                            <button type="submit" id="submit">검색</button>
                       </div>
                       
                    </div>
                </div>
                <Slider {...settings}>
                    <div className="slide slide1">
                    
                    </div>
                    <div className="slide slide2">
                       
                    </div>
                    <div className="slide slide3">
                       
                    </div>
                </Slider>
                
            </Article1>
            <div className="article2">
                <div className="row">
                <h2>어디에서나, 여행은 살아보는거야!</h2>
                    <ul className="local">
                        <li>
                            <a href="#">
                                <img src="images/hotel1.jpg" alt="" />
                                <div>
                                    <strong>리조트</strong>
                                </div>
                            </a>
                        </li>
                        <li>
                            <a href="#">
                                <img src="images/hotel1.jpg" alt="" />
                                <div>
                                    <strong>아파트</strong>
                                </div>
                            </a>
                        </li>
                        <li>
                            <a href="#">
                                <img src="images/hotel1.jpg" alt="" />
                                <div>
                                <strong>호텔</strong>
                                </div>
                            </a>
                        </li>
                        <li>
                            <a href="#">
                                <img src="images/hotel1.jpg" alt="" />
                                <div>
                                <strong>게스트하우스</strong>
                                </div>
                            </a>
                        </li>
                        <li>
                            <a href="#">
                                <img src="images/hotel1.jpg" alt="" />
                                <div>
                                <strong>호스텔</strong>
                                </div>
                            </a>
                        </li>
                        <li>
                            <a href="#">
                                <img src="images/hotel1.jpg" alt="" />
                                <div>
                                <strong>료칸</strong>
                                </div>
                            </a>
                        </li>
                    </ul>
                    {/* <div className="lifeStyle">
                        <h2>어디에서나, 여행은 살아보는거야!</h2>
                        <ul>
                            <li>
                                <img src="images/img4.jpg" alt="" />
                                <strong>집 전체</strong>
                            </li>
                            <li>
                                <img src="images/img4.jpg" alt="" />
                                <strong>집 전체</strong>
                            </li>
                            <li>
                                <img src="images/img4.jpg" alt="" />
                                <strong>집 전체</strong>
                            </li>
                        </ul>
                    </div> */}
                </div>
            </div>
            <div className="article3">
                <h2>답답할 때 떠나는 국내 여행지 TOP4</h2>
                <div className="row">
                    <div className="left">
                        <img src="images/seoul2.jpg" alt="" />
                        <p>서울 SEOUL</p>
                    </div>
                    <div className="right">
                        <div className="rightRow1">
                            <div>
                               <img src="images/gang.jpg" alt="" />
                               <p>강릉 GANGNEUNG</p>
                            </div>
                            <div>
                               <img src="images/busan1.jpg" alt="" />
                               <p>부산 BUSAN</p>
                            </div>
                        </div>
                        <div className="rightRow2">
                            <img src="images/jeju.jpg" alt="" />
                            <p>제주 JEJU</p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Homesection;